@import 'variables';
@import 'normalize';
@import 'mixins';
@import 'fonts';
@import 'components/container';
@import 'components/footer';

body{
    font-family: 'IBM Plex Sans', sans-serif;
}

.conteiner{
    margin: 0 auto;

    @include xl{
        width: 1224px;
    }
       
    @include lg{
        width: 992px;
    }

    @include md{
        width: 768px;
    }
    
    @include sm{
        width: 576px;
    }

    @include xs{
        width: 99%;
    }
}

.header{
    background-color: $darkColor;
    

    .conteiner{
        position: relative;
    }

    .nav{
        width: 50%;
        margin-left: 20px;
        padding: 28px 0;

        &__menu{
            display: flex;

            
        }

        &__item{
            position: relative;
            margin-right: 43px;

            &-name{
                display: flex;
                align-items: center;
            }

            &:hover{
                .nav__inner-menu{
                    display: block;
                    position: absolute;
                    background-color: $darkColor ;
                    padding: 5px;
                }
            }
        }

        &__link{
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
            color: $whiteColor;
            margin-right: 7px;
        }

        &__inner-menu{
            display: none;
        }

        &__inner-item{
            margin-bottom: 10px;
            border-bottom: 1px solid $whiteColor;

            &:last-child{
                margin-bottom: 0;
            }

            &:hover{
                background-color: $orangeColor;
            }
        }

        &__inner-link{
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
            color: $whiteColor;
            margin-right: 7px;
        }
    }

    &__logo{
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 96px;
        height: 96px;
        left: 50%;
        transform: translate(-50%);
        bottom: -20px;
        background-color:$darkColor;
        border-radius: 16px;

        @include md{
            left: 70%;
            width: 90px;
            height: 90px;
          }

        @include sm{
            display: none;
        }

        &-img{
            width: 64px;
            height: 64px;
        }
    }
}

.main-title{
    height: 384px;
    background-image: url("../img/main-img.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    &__title-block{
        width: 50%;
        margin-left: 50%;
        padding: 64px 0 5px 0;

        @include md{
            width: 100%;
            margin-left: 0;
          }
    }

    &__title{
        font-style: normal;
        font-weight: 500;
        font-size: 40px;
        line-height: 48px;
        color: $whiteColor;
        background-color: $orangeColor;
        max-width: max-content;
        

        @include md{
            margin: 0 auto;
          }

        &:last-of-type{
            margin-bottom: 16px;
        }
    }

    &__btn{
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $whiteColor;
        color: $orangeColor;
        padding: 12px 24px;

        @include md{
            margin: 0 auto;
          }
    }
}

.select{
    margin-bottom: 40px;

    .conteiner{
        padding: 16px 0 33px 0;
        background-color: $whiteColor;
        border-radius: 16px 16px 0 0;
        border-bottom: 1px solid #E5E5E5;
        margin-top: -60px;
    }

    &__text-block{
        text-align: center;
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 32px;
        color: $darkColor;
        margin-bottom: 24px;
    }

    &__w1{
        border-bottom: 1px solid $orangeColor;
    }

    &__w2{
        color: #4C65BD;
        border-bottom: 1px dashed #4C65BD;
    }

    &__w3{
        border-bottom: 1px solid $orangeColor;
    }

    &__w4{
        color: #4C65BD;
        border-bottom: 1px dashed #4C65BD;
    }

    &__form{
        text-align: center;
    }

    &__input{
        width: 184px;
        padding: 12px 16px;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 16px;
        color: $darkColor;
        margin-right: 24px;
        margin-bottom: 16px;
        background-image: url("../img/drop-down-dark.svg") ;
        background-repeat:  no-repeat;
        background-position:  90%;
        border: 1px solid #E5E5E5;
        border-radius: 2px;
        -webkit-appearance: none; 

        &:nth-of-type(1){
            @include lg{
                width: 30%;
            }

            @include md{
                width: 50%;
                margin-right: 0;
            }
        }

        &:nth-of-type(2){
            @include lg{
                width: 30%;
            }

            @include md{
                width: 50%;
                margin-right: 0;
            }
        }

        &:nth-of-type(3){
            @include lg{
                width: 30%;
            }

            @include md{
                width: 50%;
                margin-right: 0;
            }
        }

        &:nth-of-type(4){
            @include lg{
                width: 30%;
            }

            @include md{
                width: 50%;
                margin-right: 0;
            }
        }

        &:nth-of-type(5){
            @include lg{
                width: 30%;
            }

            @include md{
                width: 50%;
                margin-right: 0;
            }
        }

        &:disabled{
            background-color: #F5F5F5;
        }

        &:last-of-type{
            margin-right: 0;
        }
    }

    &__btn{
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 12px 80px;
        border: 1px solid #EFD8C3;
        border-radius: 2px;
        background-color: $orangeColor;
        color: $whiteColor;

        &:hover{
            background-color: $whiteColor;
            color: $orangeColor;
        }
    }
}

